.header {
  &__top {
    background-color: $color-subtitle;
    padding: 11px 0;
  }
  &__contacts {
    display: flex;
    align-items: center;
  }
  &__tel, &__geo {
    display: flex;
    align-items: center;
    color: #FFFFFF;
    font-weight: 500;
    &::before {
      content: '';
      display: block;
      order: -2;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      width: 25px;
      height: 25px;
    }
    &::after {
      content: '';
      display: block;
      order: -1;
      width: 1px;
      height: 2em;
      background-color: #203C69;
      margin-right: 12px;
      margin-left: 12px;
    }
  }
  &__tel {
    margin-right: 52px;
    &::before {
      background-image: url(../img/icon/phone.svg);
    }
  }
  &__geo {
    &::before {
      background-image: url(../img/icon/mail.svg);
    }
  }
  &__middle {
    padding: 15px 0;
  }
  &__logo {
    margin: 0 auto;
    max-width: 345px;
  }
  &__bottom {
    background-color: $color-accent2;
    padding: 16px 0;
  }
  &__menu {
    // margin: 0 auto;
  }
}

.menu {
  display: flex;
  align-items: center;
  justify-content: center;
  &__item {
    font-weight: 500;
    color: #FFFFFF;
    margin-right: 40px;
    text-decoration: underline;
    text-decoration-color: transparent;
    transition: .15s;
    &:hover {
      text-decoration-color: inherit;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  &__link {
    line-height: 1.5;
  }
}
.logo {
  &__link {}
  &__img {}
}

