.problems {
  padding-bottom: $pb-section-desctop;
  &__inner {}
  &__title {
    max-width: 760px;
  }
  &__info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 60px;
  }
  &__info-row {
    font-size: 18px;
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
      color: #6C6A72;
    }
    &:first-child {
      font-weight: 500;
    }
  }
  &__info-count {
    color: #6C6A72;
  }
  &__list {
    display: flex;

  }
}

.problems-card {
  background: #FFFFFF;
  box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
  border-radius: 10px;
  padding: 20px 15px 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__cb {
    display: block;
    width: 30px;
    height: 30px;
    border: 1px solid #000000;
    border-radius: 50%;
    margin: 0 auto;
    cursor: pointer;
    margin-bottom: 20px;
    & > input {
      // opacity: 0;
    }
  }
  &__title {
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    color: $color-subtitle;
    margin-bottom: 10px;
  }
  &__text {
    font-size: 16px;
    line-height: 24px;
    color: $color-main-text2;
    flex-grow: 1;
  }
  &__img {
    margin-bottom: 0;
    margin-right: 0;
    margin-left: auto;
  }
}