.results {
  padding-bottom: $pb-section-desctop;  //FIXME учесть пагинацию слайдера
  &__inner {}
  &__title {}
  &__list {
    // align-items: stretch;
  }
}

.results-card { //TODO так же поработать с отступами и тенью
  display: flex;
  flex-direction: column;
  &::before {
    font-weight: 900;
    font-size: 40px;
    color: #C7CFEF;
    text-align: left;
  }
  &--before::before {
    content: 'Было';
  }
  &--after::before {
    content: 'Стало';
  }
  &__box {
    box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
    background-color: #ffffff;
    border-radius: 10px;
    padding: 30px;
    flex-grow: 1;
  }
  &__header {
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid #D8E0FF;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__name {
    font-weight: 500;
    font-size: 18px;
    color: $color-subtitle;
  }
  &__info {
    color: #6C6A72;
  }
  &__text {
    // font-size: 16px;
    line-height: 1.5;
  }
}