html {

}
body {
  font-family: 'Roboto', sans-serif;
  font-size: $fz-plain-desctop;
  font-weight: 400;

  color: $color-main-text;
}
.container {
  max-width: 1220px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
  width: 100%;
}
.red-text {
  color: $color-accent2;
}
// .medium-text {
//   font-weight: 500;
// }
.btn {
  padding: 20px 40px;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: $color-accent2;
  border-radius: 10px;
  position: relative;
  z-index: 1;
  border: none;
  cursor: pointer;
  display: block;
  &::after {
    content: '';
    display: block;
    position: absolute;
    z-index: -1;
    left: 40px;
    right: 40px;
    height: 20px;
    bottom: 0;
    box-shadow: 0 0 40px 5px $color-accent2;
  }
}

.section-title {
  font-size: $fz-section-title-desctop;
  line-height: $lh-section-title-desctop;
  color: $color-accent1;
  margin-bottom: $mb-section-title-desctop;
  font-weight: 700;
}

.splide__pagination__page  {  //FIXME баг, прыгают точки при перелистывании
  display: block;
  padding: 0;
  background-color: transparent;
  width: 12px;
  height: 12px;
  border: 1px solid #D3D7F2;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
  margin-right: 17px;
  &.is-active {
  border: 1px solid #3E4DA6;
    &::after {
      content: '';
      width: 8px;
      height: 8px;
      border-radius: 50%;
      position: absolute;
      left: 1px;
      top: 1px;
      background-color: #3E4DA6;
    }
  }
}

.splide__arrow {  //FIXME стрелки прыгают
  position: absolute;
  display: block;
  z-index: 2;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent;
  border: none;
  cursor: pointer;
  &--prev {
    left: -50px;
  }
  &--next {
    right: -50px;
  }
  &[disabled] {
    opacity: 0.35;
  }
}