.quiz {
  padding-bottom: $pb-section-desctop;
  &__inner {}
  &__title {}
  &__content {}
  &__count {
    font-size: 50px;
    font-weight: 900;
    color: #D8E0FF;
    margin-bottom: 30px;
  }
  &__question {
    font-weight: 500;
    font-size: 18px;
    line-height: 1.5;
    color: $color-subtitle;
    margin-bottom: 30px;
  }
  &__row {
    display: flex;
    justify-content: space-between;
  }
  &__answer {
    position: relative;
    padding: 18px 75px 18px 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    // border: 1px solid $color-accent2;
    // color: $color-accent2;
    border: 1px solid transparent;
    color: $color-subtitle;
    border-radius: 10px;
    cursor: pointer;
    line-height: 1;
    box-shadow: 3px 4px 30px 0px rgba(62, 77, 166, 0.1);
    user-select: none;
    & > input[type="radio"] {
      padding: 0;
      margin: 0;
      cursor: pointer;
      margin-right: 10px;
    }
    &.checked {
      border: 1px solid $color-accent2;
      color: $color-accent2;
    }
    // &::after {
    //   content: '';
    //   display: block;
    //   order: -1;
    //   width: 12px;
    //   height: 12px;
    //   border: 1px solid #3E4DA6;
    //   border-radius: 50%;
    //   cursor: pointer;
    //   margin-right: 15px;
    // }
    // & > input[type="radio"]:checked + &::after {

    // }
  }
  &__btn {
    // display: flex;
    // align-items: center;
    position: relative;
    padding-right: 70px;
    // &::after { //FIXME порешать стрелку
    //   content: '';
    //   display: block;
    //   position: absolute;
    //   right: 0px;
    //   top: calc(50% - 5px);
    //   background: no-repeat center / contain;
    //   background-image: url(../img/icon/arrow-small.svg);
    //   width: 18px;
    //   height: 10px;
    // }
  }
}