.reason {
  padding-bottom: $pb-section-desctop;
  &__inner {
    // position: relative;
  }
  &__title {
    margin-bottom: 30px;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    position: relative;
    padding-right: 180px;
    padding-bottom: 85px;
  }
  &__text {
    position: relative;
    background-color: #F6F8FF;
    line-height: 24px;
    border-top-right-radius: 153px;
    padding: 30px 350px 30px 0;
    & > p {
      margin-bottom: 30px;
    }
    &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: -1;
      background-color: #F6F8FF;
      width: 50%;
      left: -100px;
      top: 0;
      height: 100%;
    }
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
  }
}